<template>
    <div id="registro">
        <div class="container-login">
            <h3 class="titulo">¿Ya tenés una cuenta? <router-link to="/login">Ingresar</router-link></h3>
            <button class="btn-google"><img src="../assets/icons/google.svg" alt="">Ingresar con google</button>
            <div class="separador"><p>o</p></div>
            <form action="" class="login-form" @submit="registrar()">
                <div class="row">
                    <div class="col-md-6">
                        <input type="text" v-model="nombre" placeholder="Nombre" required>
                        <input type="text" v-model="apellido" placeholder="Apellido" required>
                        <input type="text" v-model="dni" placeholder="Dni/Pasaporte" required>
                        <input type="text" v-model="fechaNacimiento" placeholder="Fecha de nacimiento" required>
                    </div>
                    <div class="col-md-6">
                        <input type="text" v-model="telefono" placeholder="Telefono" required>
                        <input type="text" v-model="email" placeholder="Email" required>
                        <input type="password" v-model="contraseña" placeholder="Contraseña" required>
                        <input type="password" v-model="contraseñaConfirm" placeholder="Confirmar contraseña" required>
                    </div>
                </div>
                <button type="submit">Registrarme</button>
            </form>
        </div>
    </div>
</template>

<script>
import {mapMutations} from 'vuex';
export default {
    name: 'Registro',
    data() {
        return {
            nombre: '',
            apellido: '',
            dni: '',
            fechaNacimiento: '',
            telefono: '',
            email: '',
            contraseña: '',
            contraseñaConfirm: '',
        }
    },
    methods: {
        ...mapMutations(['addUsuario','ingresar']),
        registrar: function () {
            event.preventDefault();
            let nuevoUsuario = {
                nombre: this.nombre,
                apellido: this.apellido,
                dni: this.dni,
                fechaNacimiento: this.fechaNacimiento,
                telefono: this.telefono,
                email: this.email,
                contraseña: this.contraseña
            }
            this.addUsuario(nuevoUsuario);
            this.ingresar(nuevoUsuario);
            this.$router.push({ path: '/' });
        }
    }
}
</script>

<style lang="scss">
    #registro {
        //90px tamaño del navegador, 210px tamaño del footer
        min-height: calc(100vh - 90px - 210px);
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        .container-login {
            margin: 60px 0;
            max-width: 1100px;
            background: white;
            box-shadow: 1px 1px 6px 0 #ccc;
            border-radius: 6px;
            padding: 40px 50px;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            .titulo {
                font-size: 22px;
                margin-bottom: 20px;
                a {
                    color: #de7f71;
                }
            }
            .btn-google {
                padding: 8px;
                outline: none;
                border-radius: 6px;
                background-color: #4285F4;
                color: white;
                transition: all 0.4s ease-in-out;
                border: none;
                width: 100%;
                max-width: 350px;
                img {
                    max-width: 21px;
                    max-height: 21px;
                    margin-right: 10px;
                    margin-bottom: 3px;
                }
            }
            .separador {
                position: relative;
                width: 100%;
                margin: 40px 0;
                display: flex;
                justify-content: center;
                p {
                    margin:0;
                    padding: 0 15px;
                    background: white;
                    z-index: 9999;
                    color: #aaa;
                }
            }
            .separador::after {
                content: '';
                height: 1px;
                width: 100%;
                position: absolute;
                left: 0;
                top: 50%;
                transform: translateY(-50%);
                background: #aaa;
            }
            .login-form {
                text-align: center;
                width: 100%;
                input{
                    width: 100%;
                    padding: 8px 10px;
                    border: 1px solid #ccc;
                    border-radius: 6px;
                    outline: none;
                    transition: all 0.4s ease-in-out;
                    margin-bottom: 30px;
                }
                input:focus {
                    border: 1px solid #de7f71;
                }
                button{
                    display: block;
                    margin: 0 auto 30px auto;
                    padding: 8px;
                    width: 100%;
                    max-width: 350px;
                    outline: none;
                    border-radius: 6px;
                    background-color: #de7f71;
                    color: white;
                    transition: all 0.4s ease-in-out;
                    border: none;
                }
                button:hover {
                    background-color: #ee8878;
                    color: white;
                }
            }
        }
    }
    @media (max-width: 1100px) {
        .container-login {
            margin: 20px !important;
            padding: 30px !important;
            .titulo {
                text-align: center;
            }
        }
    }
</style>